import { RouterModule, Routes } from '@angular/router';
import { NgModule } from '@angular/core';
import { SidebarComponent } from '../../shared/sidebar/sidebar.component';
import { PoliciesComponent } from './index-policies/policies.component';
import { EditPolicyComponent } from './edit-policies/edit-policy.component';

const routes: Routes = [{
  path: '',
  component: SidebarComponent,
  children: [
    { path: '', component: PoliciesComponent },
    { path: 'edit-policy/:id', component: EditPolicyComponent },
  ],
}];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class PoliciesRoutingModule {
  static components = [
    PoliciesComponent,
    EditPolicyComponent  
  ]
}
